body {
  padding: 0;
  margin: 0;
}

body {
  font-family: "Roboto", sans-serif;
  background-color: #f9f9f9;
}

#app {
  padding-top: 50px;
  text-align: center;
}

#app h1 {
  margin: 0;
  font-size: 3rem;
}

.flex-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.flex-container button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 1.5rem;
  border: none;
  border-radius: 5px;
  background-color: lightblue;
  color: white;
  cursor: pointer;
}

.flex-container button:hover {
  background-color: #007bff;
}

.flex-container button.selected {
  background-color: #007bff;
}
